@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap");
@font-face {
  font-family: "Bjorn Regular";
  src: url("../src-assets/Bjorn Regular.otf");
}

@font-face {
  font-family: "Bjorn Lite";
  src: url("../src-assets/Bjorn Light.otf");
}

@font-face {
  font-family: "Bjorn Halftone";
  src: url("../src-assets/Bjorn Halftone.otf");
}

@-webkit-keyframes scroll-fade-up {
  from {
    top: 10%;
  }
  to {
    top: 100%;
    opacity: 0;
  }
}

@keyframes scroll-fade-up {
  from {
    top: 10%;
  }
  to {
    top: 100%;
    opacity: 0;
  }
}
